// let initSqlJs = window.initSqlJs;
import config from '@/config'
import JSZip from "jszip"
import store from '@/store'

export default {
  init() {
    store.commit('SET_LOAD_STAT', 1);
    var worker = new Worker("/js/worker.sql-wasm.js");
    return worker
  },
  async loadDB() {
    store.commit('SET_LOAD_STAT', 2);
    return new Promise((resolve) => {
      let xhr = new XMLHttpRequest();
      xhr.open('GET', config.db, true);
      xhr.responseType = 'arraybuffer';
      xhr.onload = () => {
        store.commit('SET_LOAD_STAT', 3);
        JSZip.loadAsync(xhr.response).then(async zip => {
          var uInt8Array = await zip.file("db.db").async('uint8array');
          resolve(uInt8Array)
        })
      }
      xhr.send();
    })
  }
}