<template>
	<div class="index-view" id="index-view">
		<LoginView v-if="!isLogin" @ok="isLogin = true"></LoginView>
		<div v-else class="chat">
			<div class="chat-head">
				<span>KamiSama</span>
				<span class="icon icon-more" @click="showSearch = true"></span>
			</div>
			<div class="message-list" ref="message-list">
				<div class="log-view" v-if="loadStat !== 4">
					<div>{{ getLoadSatus(loadStat) }}{{ loadStat }}</div>
				</div>
				<chatMessage v-for="(item, idx) in messages" :key="idx" :message="item"></chatMessage>
			</div>
			<div class="footer">
				<div class="buttons">
					<div class="item" @click="firstPage"></div>
					<div class="item" @click="prePage"></div>
					<div class="item" @click="nextPage"></div>
					<div class="item" @click="lastPage"></div>
				</div>
			</div>
		</div>

		<Popup v-model="showSearch" :get-container="getIndexView" position="bottom" :style="{ height: '100%', width: '100%' }">
			<div class="popup">
				<SearchView @cancel="showSearch = false"></SearchView>
			</div>
		</Popup>
	</div>
</template>
<script>
	import message from '@/common/message';
	import chatMessage from '@/components/message.vue';
	import SearchView from './search';
	import LoginView from './login';
	import { mapState } from 'vuex';
	import { Popup } from 'vant';

	export default {
		components: {
			chatMessage,
			Popup,
			SearchView,
			LoginView,
		},
		computed: {
			...mapState({
				loadStat: (state) => state.loadStat,
			}),
		},
		data() {
			return {
				isLogin: false,
				showSearch: false,
				messages: [],
				page: 1,
				totalPage: 0,
			};
		},
		watch: {
			async isLogin() {
				if (this.isLogin) {
					await message.init();
					this.totalPage = message.getLastPage();
					this.getMessage();
				}
			},
		},
		methods: {
			getIndexView() {
				return document.querySelector('#index-view');
			},
			getLoadSatus(code) {
				switch (code) {
					case 0:
						return '...';
					case 1:
						return 'SQL...';
					case 2:
						return ' DB.zip ...';
					case 3:
						return ' DB.zip ...';
					case 4:
						return '';
				}
			},
			async getMessage(page = 1, scrollType = 0) {
				this.messages = await message.getMessage(page);
				this.$nextTick(() => {
					this.$refs['message-list'].scrollTop = scrollType == 0 ? 0 : this.$refs['message-list'].scrollHeight;
				});
			},
			firstPage() {
				if (this.page <= 1) {
					return;
				}
				this.page = 1;
				this.getMessage(this.page);
			},
			lastPage() {
				this.page = this.totalPage;
				this.getMessage(this.page);
			},
			nextPage() {
				if (this.page >= this.totalPage) {
					return;
				}
				this.page++;
				this.getMessage(this.page);
			},
			prePage() {
				if (this.page > 1) {
					this.page--;
					this.getMessage(this.page, 1);
				}
			},
		},
		async mounted() {
			// await message.init();
			// this.totalPage = message.getLastPage();
			// this.getMessage();
		},
	};
</script>
