<template>
	<div class="message-view">
		<!-- : 1 -->
		<div class="message-text-container clearfix" v-if="message.m_uiMessageType == 1">
			<img :class="'message-avatar ' + (isMe ? 'right' : 'left')" />
			<div :class="isMe ? 'right' : 'left'">
				<span :class="isMe ? 'arrow-right right' : 'arrow-left left'"></span>
				<div :class="'inline-block ' + (isMe ? 'message-content' : 'message-content-left')">
					{{ message.m_nsContent }}
				</div>
			</div>
		</div>

		<!-- : 47 -->
		<div class="message-text-container clearfix" v-if="message.m_uiMessageType == 47">
			<img :class="'message-avatar ' + (isMe ? 'right' : 'left')" />
			<div :class="isMe ? 'right' : 'left'">
				<img referrerPolicy="no-referrer" class="message-img" :src="stickerUrl" />
			</div>
		</div>

		<!-- : 3 -->
		<div class="message-text-container clearfix" v-if="message.m_uiMessageType == 3">
			<img referrerPolicy="no-referrer" :class="'message-avatar ' + (isMe ? 'right' : 'left')" />
			<div :class="isMe ? 'right' : 'left'">
				<img class="message-img" :data-src="imgPrefix + message.m_nsContent" v-if="!imageOriginError" @error="imageOriginError = true" :src="imgPrefix + message.m_nsContent" />
				<img
					referrerPolicy="no-referrer"
					v-else-if="!imageThumbError"
					class="message-img"
					:data-src="imgPrefix + getImageThumb(message.m_nsContent)"
					@error="imageThumbError = true"
					:src="imgPrefix + getImageThumb(message.m_nsContent)"
				/>
				<img referrerPolicy="no-referrer" v-else class="message-img" src="@/assets/images/pic_delete.png" />
			</div>
		</div>

		<!-- : 34 -->
		<div class="message-text-container clearfix" v-if="message.m_uiMessageType == 34" @click="playAudio">
			<img :class="'message-avatar ' + (isMe ? 'right' : 'left')" />
			<div :class="isMe ? 'relative right' : 'relative left'">
				<span :class="isMe ? 'arrow-right right' : 'arrow-left left'"></span>
				<div :class="'inline-block ' + (isMe ? 'message-content' : 'message-content-left')">
					<audio ref="audio" :data-id="message.id" :src="imgPrefix + message.m_nsContent"></audio>
					<span
						:class="'inline-block voice-img ' + (isMe ? 'voice-right-img' : 'voice-left-img') + (audioState ? (isMe ? ' voice_right_click' : ' voice_left_click') : '')"
						:style="{ width: 2 + message.m_uiVoiceTime - 1 + 'rem' }"
					></span>
					<span :class="'voice-duration ' + (isMe ? 'voice-duration-right' : '')">{{ message.m_uiVoiceTime }}''</span>
				</div>
			</div>
		</div>

		<!--  -->
	</div>
</template>
<script>
	import { imgPrefix } from '@/config';
	let parseString = require('xml2js').parseString;
	export default {
		props: {
			message: {
				type: Object,
				default() {
					return {};
				},
			},
		},
		data() {
			return {
				imgPrefix: imgPrefix,
				imageOriginError: false,
				imageThumbError: false,
				audioState: false,
			};
		},
		asyncComputed: {
			async stickerUrl() {
				let decodeFun = (xml) => {
					return new Promise((resolve) => {
						parseString(xml, (err, result) => {
							if (!err) {
								resolve(result);
							}
						});
					});
				};
				let ret = await decodeFun(this.message.m_nsContent);
				if (ret.msg && ret.msg.emoji) {
					return ret.msg.emoji[0].$.cdnurl;
				}
				return '';
			},
		},
		computed: {
			isMe() {
				return !this.message.m_nsToUsr;
			},
		},
		mounted() {},
		methods: {
			playAudio() {
				//  
				let audio = this.$refs['audio'];
				audio.onplay = () => {
					this.audioState = true;
				};
				audio.onpause = () => {
					this.audioState = false;
				};
				if (audio.paused) {
					audio.play();
				} else {
					audio.pause();
				}
			},
			getImageThumb(src) {
				return src.replace('.jpg', '_thum.jpg');
			},
		},
	};
</script>
