<template>
	<div class="search-view">
		<Search placeholder="" v-model="searchKey" @search="onSearch" @cancel="onCancel" show-action></Search>
		<div class="quick-search">
			<p></p>
			<div class="links">
				<div class="group">
					<span></span>
					<span></span>
					<span></span>
				</div>
				<div class="group">
					<span></span>
					<span></span>
					<span></span>
				</div>
				<div class="group">
					<span></span>
				</div>
			</div>
		</div>
		<p v-for="(item, idx) in searchResult" :key="idx">{{ item.m_nsContent }}</p>
	</div>
</template>
<script>
	import { Search } from 'vant';
	import message from '@/common/message';
	export default {
		components: {
			Search,
		},
		data() {
			return {
				searchKey: '',
				searchResult: [],
			};
		},
		methods: {
			async onSearch() {
				console.log('sarch: ', this.searchKey);
				if (!this.searchKey) {
					return;
				}
				let ret = await message.getMessage(1, {
					m_uiMessageType: 1,
					m_nsContent: {
						op: 'LIKE',
						value: this.searchKey,
					},
				});
				this.searchResult = ret;
				console.log(ret);
			},
			onCancel() {
				this.$emit('cancel', true);
			},
		},
	};
</script>
