import sql from './sql';
import NProgress from 'nprogress'
import store from '@/store'
const worker = sql.init();

// 
// select id, (select count(*) from message b where a.id >= b.id ) as row_number from message a WHERE a.id == 999
`SELECT t.*
FROM (
	SELECT id
	FROM MESSAGE
	ORDER BY id ASC
	LIMIT 100 OFFSET (999-50 + 100)
) q
JOIN message t
ON t.id = q.id
`

worker.onmessage = (e) => {
  switch (e.data.id) {
    case 1:
      // 
      console.log("Loading database from file", e)
      window.$EventBus.$emit('openDB', {
        ...e.data
      })
      break;
    case 2:
      // 
      window.$EventBus.$emit('getTotalMessage', {
        ...e.data
      })
      break;
    case 3:
      // 
      window.$EventBus.$emit('execSQL', {
        ...e.data
      })
      break;
  }
}
export default {
  worker: null,
  page: 1,
  pageSize: 100,
  total: 0,
  //   0 
  offset: 0,
  query: {
    // m_uiMessageType: 34
  },
  async init() {
    NProgress.start();
    this.worker = worker;
    const dbStream = await sql.loadDB();
    this.page = 1;
    this.pageSize = 100;
    this.total = 0;
    worker.postMessage({
      id: 1,
      action: "open",
      buffer: dbStream,
    });
    return new Promise((resolve) => {
      window.$EventBus.$on('openDB', async () => {
        await this.getTotalMessage();
        NProgress.done();
        store.commit('SET_LOAD_STAT', 4)
        resolve()
      })
    })
  },

  // 
  getTotalMessage() {
    console.log(`SELECT COUNT(*) AS 'total' FROM MESSAGE ${this.getQueryString()}`)
    return new Promise((resolve) => {
      worker.postMessage({
        id: 2,
        action: 'exec',
        sql: `SELECT COUNT(*) AS 'total' FROM MESSAGE ${this.getQueryString()}`,
      })
      window.$EventBus.$on('getTotalMessage', (data) => {
        let ret = data.results
        if (ret[0] && ret[0].values) {
          this.total = ret[0].values[0][0]
          console.log(this.total)
          resolve(this.total)
        }
      })
    })
  },

  // 
  getQueryString() {
    let prefix = 'WHERE ';
    let index = 0;
    for (const key in this.query) {
      const val = this.query[key];
      let quryStr = '';
      switch (typeof val) {
        case 'string':
          quryStr = `${key} == '${val}'`
          break;
        case 'number':
          quryStr = `${key} == ${val}`
          break;
        case 'object':
          if (val.op == 'LIKE') {
            quryStr = `${key} like '%${val.value}%' or ${key} like '${val.value}%' or ${key} like '%${val.value}' or ${key} like '${val.value}'`
          }
          break;
      }
      prefix += ((index == 0 ? '' : ' and ') + quryStr + ' ');
      index++
    }
    if (index == 0) {
      return ''
    }
    return prefix
  },

  getLastPage() {
    return parseInt((this.total - this.offset) / this.pageSize) + ((this.total - this.offset) % this.pageSize > 0 ? 1 : 0)
  },

  // sql {key, value} 
  formatSelect(ret) {
    let result = [];
    if (ret[0] && ret[0].values && ret[0].columns) {
      ret[0].values.map((item) => {
        let struct = {};
        item.map((val, idx) => {
          struct[ret[0].columns[idx]] = val
        })
        result.push(struct);
      })
      return result;
    }
  },
  //  
  getMessage(page = 1, query) {
    //  
    if (page > this.total) {
      page = this.getLastPage()
    }
    // 
    if (query) {
      this.query = query
    }
    NProgress.start();
    // 
    return new Promise((resolve) => {
      const sql = `SELECT t.*
      FROM (
        SELECT id
        FROM MESSAGE
        ${this.getQueryString()}
        ORDER BY id ASC
        LIMIT ${this.pageSize} OFFSET ( ${this.offset ? this.offset - 50 : 0} + ${this.pageSize * (page - 1)})
      ) q
      JOIN message t
      ON t.id = q.id`;
      console.debug(sql)
      worker.postMessage({
        id: 3,
        action: 'exec',
        sql
      })
      window.$EventBus.$on('execSQL', (data) => {
        NProgress.done();
        let ret = data.results
        resolve(this.formatSelect(ret))
      })
    })
  }
}